.resize-handle {
  background: #e6e6e6;
  position: relative;
  display: inline-block;
}
.resize-handle-no-drag {
  cursor: not-allowed !important;
}
.react-draggable-dragging {
  z-index: 1;
}
.react-draggable-dragging-sibling {
  pointer-events: none;
  user-select: none;
}
.resize-handle-e,
.resize-handle-w {
  width: 10px;
  cursor: e-resize;
}
.resize-handle-w {
  cursor: w-resize;
}
.resize-handle-button-w,
.resize-handle-button-e {
  cursor: pointer;
  height: 30%;
  min-height: 40px;
  max-height: 110px;
  width: 10px;
  background-color: #c6c6c6;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}
.resize-handle-button-w::after,
.resize-handle-button-e::after {
  content: "";
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 2px;
  border: 6px solid transparent;
  margin-top: -5px;
}
.resize-handle-button-w:hover,
.resize-handle-button-e:hover {
  background: #999;
}
.resize-handle-button-w {
  border-radius: 18px 2px 2px 18px;
}
.resize-handle-button-w::after {
  border-right-color: #666;
  left: -4px;
}
.resize-handle-button-w:hover::after {
  border-right-color: #fff;
}
.resize-handle-button-e {
  border-radius: 2px 18px 18px 2px;
}
.resize-handle-button-e::after {
  border-left-color: #666;
}
.resize-handle-button-e:hover::after {
  border-left-color: #fff;
}
.resize-handle-button-n,
.resize-handle-button-s {
  cursor: pointer;
  height: 10px;
  width: 50%;
  max-width: 110px;
  min-width: 40px;
  background-color: #c6c6c6;
  position: absolute;
  left: 0;
  left: 50%;
  transform: translate(-50%);
}
.resize-handle-button-n::after,
.resize-handle-button-s::after {
  content: "";
  display: inline-block;
  position: absolute;
  left: 50%;
  top: 2px;
  border: 6px solid transparent;
  margin-left: -5px;
}
.resize-handle-button-n:hover,
.resize-handle-button-s:hover {
  background: #999;
}
.resize-handle-button-n {
  border-radius: 18px 18px 2px 2px;
}
.resize-handle-button-n::after {
  border-bottom-color: #666;
  top: -4px;
}
.resize-handle-button-n:hover::after {
  border-bottom-color: #fff;
}
.resize-handle-button-s {
  border-radius: 2px 2px 18px 18px;
}
.resize-handle-button-s::after {
  border-top-color: #666;
}
.resize-handle-button-s:hover::after {
  border-top-color: #fff;
}
.resize-handle-n,
.resize-handle-s {
  width: 100%;
  height: 10px;
  cursor: n-resize;
}
.resize-handle-s {
  cursor: s-resize;
}
.inline-block-box {
  display: inline-block;
}
